<template>
    <div>
        <div class="mb-3 mb-xl-5">
            <h2>3.3 </h2>
            <p>
                <mark>flex</mark>
                flex-grow, flex-shrink  flex-basis
            </p>
            <ul>
                <li>
                    <mark>flex: 1</mark>
                    
                    <mark>flex-grow: 1</mark>
                    
                </li>
                <li>
                    <mark>flex: none</mark>
                    
                    <mark>flex-shrink: 0</mark>
                    
                </li>
            </ul>
        </div>


        <div class="container">
        </div>
    </div>
</template>

<script>
    export default {
        name: "Flex",
        data() {
            return {
                cssCode: "#wrapper{" + "\n" +
                    "    display: flex;" + "\n" +
                    "    width: 400px;" + "\n" +
                    "}" + "\n" + "\n" +
                    "#A, #B, #C{" + "\n" +
                    "    width: 200px;" + "\n" +
                    "}",
                cssCodeBak: "",

                direction: "row",

                axis: {
                    row: 0,
                    rowReverse: 180,
                    column: 90,
                    columnReverse: 270
                },

                axisRotate: 0,

                //ABC
                prefix1: [...Array(3).keys()].map(i => String.fromCharCode(i + 65)),
                //123
                prefix2: [...Array(3).keys()].map(i => i + 1),
                //1.1, 2.2, 3.1
                prefix3: [...Array(3).keys()].map(i => "." + (i + 1)),

                boxes: {
                    prop: {
                        text: "wrapper",
                        active: false,
                    },
                    child: [
                        {
                            prop: {
                                text: "A",
                                active: false,
                                flexGrow: 0,
                            },
                            child: []
                        },
                        {
                            prop: {
                                text: "B",
                                active: false,
                                flexGrow: 0,
                            },
                            child: []
                        },
                        {
                            prop: {
                                text: "C",
                                active: false,
                                flexGrow: 0,
                            },
                            child: []
                        },
                    ]
                },
                htmlCode: "",
                htmlCodeHeight: 0,
                cssCodeHeight: 450,
            }
        },
        created() {
            //this.cssCodeBak = this.cssCode;
            //this.updateHtmlCode();
            //this.updateCssCode();
        },
        mounted() {
            //this.htmlCodeHeight = this.$refs.htmlCode.scrollHeight;
        },
        methods: {
            //
            toCamel(str) {
                return str.replace(/([^-])(?:-+([^-]))/g, function ($0, $1, $2) {
                    return $1 + $2.toUpperCase();
                });
            },
            updateHtmlCode() {
                let _self = this;

                this.htmlCode = "<div id=\"wrapper\">";
                let depth = 0;

                fun(this.boxes);

                function fun(boxes) {
                    depth++;
                    for (let index in boxes.child) {
                        let value = boxes.child[index];
                        _self.htmlCode += "\n";
                        for (let i = 0; i < depth; i++) {
                            _self.htmlCode += "    ";
                        }
                        _self.htmlCode += "<div id=\"" + value.prop.text + "\">";

                        if (value.child.length > 0) {
                            fun(value);
                            _self.htmlCode += "\n";
                            for (let i = 0; i < depth; i++) {
                                _self.htmlCode += "    ";
                            }
                        }
                        _self.htmlCode += "</div>";
                    }
                    depth--;
                }

                this.htmlCode += "\n";
                this.htmlCode += '</div>';
                if (this.$refs.htmlCode) this.htmlCodeHeight = this.$refs.htmlCode.scrollHeight;
            },
            //
            boxMouseOver(boxes, parentBox) {
                if (parentBox) {
                    this.$set(parentBox.prop, "active", false);
                }
                this.$set(boxes.prop, "active", true);
            },
            //
            boxMouseLeave(boxes) {
                this.$set(boxes.prop, "active", false);
            },
            /**
             * 
             * @param boxes         
             * @param prefixArgs    
             */
            boxChildAdd(boxes, ...prefixArgs) {
                let text = "";

                //
                for (let k in prefixArgs) {
                    //
                    let argPrefixLength = parseInt(k) + 1;
                    //
                    let childCount = prefixArgs[k];
                    //
                    if (!this[`prefix${argPrefixLength}`][childCount]) {
                        return false;
                    }
                    text += this[`prefix${argPrefixLength}`][childCount];
                }

                //
                let argPrefixLength = prefixArgs.length + 1;

                //
                let childCount = boxes.child.length;

                //
                if (!this[`prefix${argPrefixLength}`][childCount]) {
                    return false;
                }
                text += this[`prefix${argPrefixLength}`][childCount];

                this.$set(boxes.child, childCount, {
                    prop: {
                        text,
                        active: false,
                    },
                    child: []
                });
                this.updateHtmlCode();
            },
            updateCssCode() {
                let cssCode = "\n\n";
                for (let key in this.boxes.child) {
                    let value = this.boxes.child[key];
                    cssCode += "#" + value.prop.text + "{" + "\n" +
                        "    flex-grow: " + value.prop.flexGrow + ";" + "\n" +
                        "}\n";
                }
                this.cssCode = this.cssCodeBak + cssCode;
            }
        },

        watch: {
            direction: function (newD, oldD) {
                this.updateHtmlCode();
                this.axisRotate = this.axis[this.toCamel(newD)];
            }
        },

    }
</script>

<style scoped>

    .box {
        display: flex;
        flex-grow: 0;
        box-sizing: border-box;
        margin: 6px;
        border: 1px solid #fff;
        background: rgba(0, 123, 255, 0.3);
    }

    .box > p {
        padding: 0.4em;
        margin: 0.2em 0 0.2em;
    }

    .box-empty {
        min-width: 38px;
        min-height: 38px;
    }

    #box-wrapper {
        width: 430px;
        min-height: 400px;
    }

    .box:hover {
        cursor: pointer;
        border: 1px dotted #2c3e50;
    }
</style>